#igf-adm-folder-shortcode {
  width: 100%;
  text-align: center;
}

#igf-adm-folder-name {
  width: 100%;
}

#igf-adm-add-subfolder {
  float: right;
}

#igf-adm-folder-path {
  background: #eee;
  padding: 3px 5px;

  li {
    display: inline;
    list-style-type: none;
  }
}

#igf-adm-add-folder-files {
  float: right;
}

#igf-adm-empty-folder-message {
  font-style: italic;
}

#igf-adm-folder-files-metabox {
  overflow: auto;
}

#igf-adm-folder-files {
  table-layout: fixed;
  width: 100%;
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px solid #eee;

  td {
    padding: 2px 5px;
    word-wrap: break-word;
  }

  tr:hover {
    td {
      background: #eee;
    }
  }

  .igf-adm-icon {
    width: 10%;

    img {
      cursor: move;
      width: 100%;
      max-width: 48px;
    }

  }

  .igf-adm-file-name {
    .igf-adm-title {
      display: block;
      font-weight: bold;
    }
  }

  .igf-adm-size {
    width: 15%;
  }

  .igf-adm-downloads {
    width: 10%;
  }

  .igf-adm-shortcode {
    position: relative;
    width: 20%;
    text-align: center;

    input {
      width: 100%;
      font-size: 80%;
      text-align: center;
    }
  }

  .igf-adm-actions {
    width: 10%;
    text-align: right;

    a {
      text-decoration: none;
    }

    .igf-adm-remove {
      color: #a00;

      &:hover {
        color: red;
      }
    }
  }
}

#igf-adm-subfolders-metabox {
  overflow: auto;

  a {
    text-decoration: none;
  }
}

#igf-adm-subfolders {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px solid #eee;

  .igf-adm-subfolder-icon {
    cursor: move;
  }
}

.igf-adm-file-shortcode-copied {
  position: absolute;
  top: 0;
  left: 0;
  background-color: yellow;
  color: red;
  width: 100%;
  display: none;
}

.igf-adm-shortcode-tooltip {
  display: none;
  position: absolute;
  background-color: #f8f9c4;
  padding: 3px 10px;
  font-size: 80%;
  font-weight: bold;
}

